import React from "react"

export default class PageNotFound extends React.Component {
    render() {
        return (
            <div className='std-page center-page-x center' style={{ textAlign: 'center' }}>
                <br></br><br></br>
                <span style={{ fontSize: "80px", color: 'orange' }}>404</span>
                Sorry, Page Not Found
            </div>
        );
    }
}